'use client'
import { useState } from "react";
import { DropdownFilterProps } from "./DropdownFilter.interface";
import './style.scss'
import TriangleImage from '@/public/images/Triangle.png'
import Image from "next/image";

export default function DropdownFilter(props: DropdownFilterProps) {

    const [openStatus, setOpenStatus] = useState<boolean>(false);
    const [activeElementIndex, setActiveElementIndex] = useState<number>(0)

    return (
        <div className="relative min-h-[60px] min-w-[195px] bg-[#001E51] dropdown">
            <div className="w-full flex flex-col justify-start">
                {/* <div className="absolute inset-0 min-h-[60px] h-[60px] flex flex-row justify-start items-center pl-[10px] pr-[10px]">
                    <div className="w-full h-[60px] flex items-center">
                        <p>{props.elements[activeElementIndex].title}</p>
                    </div>
                    <Image alt='Стрелка выпадающего списка' className="pointer-none" src={TriangleImage} />
                </div> */}
                <div className="max-h-[240px] overflow-y-auto over">
                    {props.elements.map((element) => {
                        return (
                            <div
                                key={element.title}
                                className="w-full h-[60px] flex items-center"
                                onClick={() => { props.handleSelectSetState(element.returnedValue) }}
                            >
                                {element.title}
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
}
